import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-gims-home',
  templateUrl: './gims-home.component.html',
  styleUrls: ['./gims-home.component.css']
})

export class GimsHomeComponent implements OnInit {
  title = 'GIMS';
  foods = [
    {value: 'steak-0', viewValue: 'Steak'},
    {value: 'pizza-1', viewValue: 'Pizza'},
    {value: 'tacos-2', viewValue: 'Tacos'}
  ];
  folders = [
    {
      name: 'Photos',
      updated: new Date('1/1/16'),
    },
    {
      name: 'Recipes',
      updated: new Date('1/17/16'),
    },
    {
      name: 'Work',
      updated: new Date('1/28/16'),
    }
  ];
  notes = [
    {
      name: 'Vacation Itinerary',
      updated: new Date('2/20/16'),
    },
    {
      name: 'Kitchen Remodel',
      updated: new Date('1/18/16'),
    }
  ];

  private menuStatus:boolean = false;
  constructor() {
  }

  ngOnInit() {
  }

  menuSlide(event){
    console.log("Event : " + event);
    this.menuStatus = event;
    console.log("menuStatus : " + this.menuStatus);    
  }

}



