import { NgModule , ModuleWithProviders, Component } from '@angular/core';
import { Routes , RouterModule } from '@angular/router';
import { GimsHomeComponent } from '../gims-home/gims-home.component';
import { GimsAppScreenComponent } from '../gims-app-screen/gims-app-screen.component';
// GIMS-routes
const routes: ModuleWithProviders = RouterModule.forRoot([
 {path: 'gims_home', component : GimsHomeComponent},
 {path: 'app', component: GimsHomeComponent},
 {path: 'register', component: GimsAppScreenComponent },
 {path: '', redirectTo: '/gims_home', pathMatch: 'full' },
], { useHash: true});
@NgModule({
    imports: [routes],
    exports: [RouterModule],
    providers : []
})
export class GimsRoutingModule {

}



